(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .directive('customField', customField);

  /** @ngInject */
  function customField() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/customFieldDirective/customFieldDirective.html',
      scope: {
        data: '='
      },
      controller: CustomFieldDirectiveController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;
  }

  /** @ngInject */
  function CustomFieldDirectiveController(
    $log,
    $stateParams,
    projectCustomField,
    CUSTOM_FIELD_TYPES
  ) {
    var vm = this;
    var projectId = $stateParams.projectId;
    vm.typeOptions = CUSTOM_FIELD_TYPES;
    vm.store = store;

    angular.element(document).ready(setFieldType);

    function store() {
      // vm.data['type'] = 'project_custom_field';
      vm.data['type'] = vm.type.value;
      $log.debug('customFieldData');
      $log.debug(vm.data);

      switch (vm.type.value) {
        case 'RADIO_BUTTON':
          setRadioButtonMetadataValue(vm.data.metadata);
          break;
      }

      removeDateTime();

      $log.debug('vm.data');
      $log.debug(vm.data);

      projectCustomField
        .storeOrUpdateByProjectId(vm.data, projectId);
    }

    function setRadioButtonMetadataValue(metadata) {
      angular.forEach(metadata.options, function(item, key) {
        item['value'] = key;
      });
    }

    function removeDateTime() {
      if (angular.isDefined(vm.data.created_at)) {
        delete vm.data.created_at;
      }

      if (angular.isDefined(vm.data.updated_at)) {
        delete vm.data.updated_at;
      }
    }

    function setFieldType() {
      $log.debug('=== setFieldType() ===');
      $log.debug(vm.data);
      if (angular.isDefined(vm.data.type)) {
        $log.debug('=== type is defined ===');

        angular.forEach(CUSTOM_FIELD_TYPES, function(type) {
          if (vm.data.type == type.value) {
            vm.type = type;
          }
        });
      } else {
        $log.debug('=== type is not defined ===');

        vm.type = {
          label: '',
          typeName: 'text',
          value: 'TEXT'
        };
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .directive('editableProjectHyperlinks', editableProjectHyperlinks);

  /** @ngInject */
  function editableProjectHyperlinks() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/editableProjectHyperlinksDirective/editableProjectHyperlinksDirective.html',
      scope: {
        projectId: '=',
        data: '='
      },
      controller: EditableProjectHyperlinksDirectiveController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;
  }

  /** @ngInject */
  function EditableProjectHyperlinksDirectiveController($log, projectHyperlink) {
    var vm = this;
    vm.data = [];

    if (vm.data.length == 0) {
      vm.data.push({
        name: "",
        link: ""
      });
    }

    $log.debug("EditableProjectHyperlinksDirectiveController");

    $log.debug("vm.projectId = " + vm.projectId);

    if (angular.isDefined(vm.projectId)) {
      $log.debug("vm.projectId is defined.");
      $log.debug("vm.projectId = " + vm.projectId);

      var onSuccess = function(value) {
        vm.data = value.data;
      };

      projectHyperlink
        .getByProjectId(vm.projectId)
        .then(onSuccess);
    }

    vm.remove = function(index) {
      $log.debug("remove a project hyperlink");
      $log.debug(index);

      var onSuccess = function() {
        vm.data.splice(index, 1);

        $log.debug(vm.data);
      };

      var onFailure = function(response) {
        $log.error(response);
      };

      if (angular.isDefined(vm.data[index].id)) {
        projectHyperlink
          .dropByProjectIdAndHyperlinkId(vm.projectId, vm.data[index].id)
          .then(onSuccess)
          .catch(onFailure);
      } else {
        vm.data.splice(index, 1);
      }
    }

    vm.append = function() {
      vm.data.push({
        name: "",
        link: ""
      });
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .filter('customFieldDataToText', customFieldDataToText);

  /** @ngInject */
  function customFieldDataToText($filter, $log) {
    var filter = function(input, type, metadata) {
      var transformedInput;

      switch (type) {
        case 'RADIO_BUTTON':
          transformRadioButton();

          break;
        case 'TEXT':
          transformedInput = input.value;

          break;
        default:
          transformedInput = 'NONE';
      }

      $log.debug('### type ###');
      $log.debug(type);
      $log.debug('### transformedInput ###');
      $log.debug(transformedInput);

      return transformedInput;

      function transformRadioButton() {
        $log.debug('=== transformRadioButton ===');
        $log.debug('### input ###');
        $log.debug(input);
        $log.debug('### metadata ###');
        $log.debug(metadata);

        angular.forEach(metadata.options, function(item) {
          $log.debug(item);

          if (item.value == input.option.value) {
            transformedInput = item.display_name;
            $log.debug('--- equal ---');
          }
        });
      }
    };

    return filter;
  }
})();

