(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .factory('alertMessage', alertMessage);

  /** @ngInject */
  function alertMessage(
    $log
  ) {
    var service = {
      convertToValidationDanger: convertToValidationDanger,
      convertToDanger: convertToDanger
    };

    return service;

    function convertToValidationDanger(errors) {
      $log.debug("convertToValidationDanger()");

      var data = [];

      angular.forEach(errors, function(messages, fieldName) {
        var item = {
          field: fieldName,
          message: []
        };

        messages.forEach(function(message) {
          item.message.push('alert.danger.' + message);
        });

        this.push(item);
      }, data);

      var danger = {
        type: 'danger',
        data: data
      };

      return danger;
    }

    function convertToDanger(errors) {
      $log.debug("convertToDanger()");

      var message = [];

      errors.forEach(function(item) {
        message.push('alert.danger.' + item);
      });

      var danger = {
        type: 'danger',
        data: {
          message: message
        }
      };

      $log.debug(danger);

      return danger;
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .controller('ProfileController', ProfileController);

  /** @ngInject */

  function ProfileController($uibModal, cities, $log, defaultAvatarPath, userProfile) {

    var vm = this;

    vm.open = function() {
      var modal = $uibModal.open({
        templateUrl: 'myModalContent.html',
        controller: 'ModalInstanceCtrl',
        controllerAs: 'vm'
      });
    };

    angular.element(document).ready(getProfile());

    function getProfile() {
      var doneCallbacks = function(result) {
          $log.debug("getProfile() done");
          $log.debug(result);

          vm.profile = result.data;
        },
        failCallbacks = function(response) {
          $log.debug(response);
        };

      userProfile.get().then(doneCallbacks, failCallbacks)
    }
  }

  angular.module('vmsFrontend').controller('ModalInstanceCtrl', function($state, $uibModalInstance, $log, vmsLocalStorage, userProfile) {

    var vm = this;
    vm.remind = "";

    vm.ok = function() {
      $log.debug('deleteClickOk');

      var credentials = {
        username: vmsLocalStorage.getUsername(),
        password: vm.password
      };

      $log.debug("username = " + credentials.username);
      $log.debug("password = " + credentials.password);

      userProfile
        .drop(credentials)
        .then(function(response) {
          $log.debug("delete success");
          $uibModalInstance.close();

          $state.go('login');
        })
        .catch(function(response) {
          $log.debug("delete failure");
          $log.debug(response);

          if (response.status == 401) {
            vm.deleteMsg = "";
          }
        });
    };

    vm.cancel = function() {
      $uibModalInstance.dismiss('cancel');
    };
  });

})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .controller('VolunteerExperienceController', VolunteerExperienceController);

  function VolunteerExperienceController($log, userExperience, $filter) {
    var vm = this;
    var beginYear = 1970;
    vm.years = new Array();
    vm.isShowAddExperience = false;

    angular.element(document).ready(getExperiences);
    angular.element(document).ready(setYear);

    function getExperiences() {
      var onSuccess = function(value) {
        $log.debug(value);

        vm.experiences = value.experiences;

        if (angular.isDefined(value)) {
          $log.debug('vm.experiences.length');
          $log.debug(vm.experiences.length);

          if (vm.experiences.length == 0) {
            vm.isShowAddExperience = true;
          }
        } else {
          $log.debug('experience value is not defined');

          vm.isShowAddExperience = true;
        }

      };

      userExperience
        .getAll()
        .then(onSuccess);
    }

    function setYear() {
      var nowDate = new Date();

      for (var year = beginYear; year <= nowDate.getFullYear(); year++) {
        vm.years.push({
          value: year,
          text: year + ' '
        });
      }
    }

    function getEditableObject(id) {
      var objName = 'experience' + id;
      var editableObj = vm[objName];

      return editableObj;
    }

    function transformToDate(year) {
      return new Date(year, 1);
    }

    vm.addExperience = function() {

      var onSuccess = function(data) {
        getExperiences();
        vm.isShowAddExperience = false;
      };
      var onFailure = function(data) {};

      $log.debug('addExperiences()');
      $log.debug(vm.sExperience);

      userExperience
        .create(vm.sExperience)
        .then(onSuccess)
        .catch(onFailure);
    };

    vm.updateExperience = function(id) {
      var getByIdFilter = $filter('getById');
      var experience = getByIdFilter(vm.experiences, id);

      $log.debug(experience);

      var onSuccess = function() {
        getEditableObject(id).$hide();
      };
      var onFailure = function() {
        /**
         * TODO: show error msg
         */
      };

      userExperience
        .update(experience)
        .then(onSuccess)
        .catch(onFailure);
    };

    vm.deleteExperience = function(id) {
      var onSuccess = function() {
        getEditableObject(id).$hide();
        getExperiences();
      };
      var onFailure = function() {
        /**
         * TODO: show error msg
         */
      };

      userExperience
        .dropById(id)
        .then(onSuccess)
        .catch(onFailure);
    };

    vm.editableFormShow = function(id) {

      getEditableObject(id).$show();
    };

    vm.editableIsVisible = function(id) {

      $log.debug('vm.editableIsVisible()');

      var editableObj = getEditableObject(id);

      if (editableObj == null) {
        return false;
      }

      var visible = editableObj.$visible;

      return visible;
    };

    vm.editableFormCancel = function(id) {
      getEditableObject(id).$cancel();
    };

    vm.setAddExperienceVisible = function() {
      vm.isShowAddExperience = true;
    };

    vm.setAddExperienceInvisible = function() {
      vm.isShowAddExperience = false;
    };

    vm.onShowEditableForm = function(id) {
      var formElement = angular.element(document).find('form[name="vm.experience' + id + '"]');
      formElement.addClass('editable-form-section');
    };

    vm.onHideEditableForm = function(id) {
      var formElement = angular.element(document).find('form[name="vm.experience' + id + '"]');
      formElement.removeClass('editable-form-section');
    }
  }
})();

