      var base64Image = "data:" + object.filetype + ';base64,' + object.base64;

      userProfile
        .updateAvatar(base64Image, true)
        .then(function(value) {
          var data = value.data;

          vm.profile.avatar_url = data.avatar_url;
        })

      vm.avatar = base64Image;
